import React from 'react'
import ReactDOM from 'react-dom'
import MyIcon from '.';
import { render, renderer } from '../../test.setup';

describe('MyIcon', () => {
  it('renders component without crashing', () => {
    const div = document.createElement('div');
    ReactDOM.render(<MyIcon source={"source"} />, div);
    expect(div).toEqual(<p>source</p>).toMatchSnapshot();
  });

});